.main {
  padding: 30px 0;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imageBlock {
  overflow: hidden;
}
.image {
  border-radius: 20px;
  width: 600px;
  margin-bottom: 30px;
}
.text {
  text-align: center;
  font-size: 20px;
  color: var(--main-color);
  padding: 10px 15px;
}
.text:hover {
  background-color: var(--main-color);
  color: var(--second-color);
  border-radius: 10px;
}

@media screen and (min-width: 768px) {
  .main {
    padding: 60px 0;
  }
}
